.advantages-list {
    display: flex;
    flex-direction: column;
    margin-left: 10.5px;
    list-style-type: none;
    @media (min-width: @tablet-width){
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 0;
        margin-left: 0;
        width: 629px;
        border-bottom: 1px solid #E6E6E6;
        margin-bottom: 0;
        padding-bottom: 47px;
    }
    @media (min-width: @desktop-width) {
        justify-content: space-between;
        width: 1224px;
        border: none;
        margin-left: 0;
        padding-bottom: 69px;
    }
}
.advantages__item {
    margin-bottom: 20px;
    @media (min-width: @tablet-width){
        width: 230px;
        height: 280px;
    }
    @media (min-width: @desktop-width) {
        max-width: 230px;
        

    }
}

.advantages__item--healthy {
    @media (min-width: @tablet-width) {
        margin-bottom: 37px;
    }
    @media (min-width: @desktop-width) {
        margin-right: 0;
    }
}

.advantages__item--easy {
    @media (min-width: @tablet-width) {
        margin-bottom: 37px;
    }
    @media (min-width: @tablet-width) {
        width: 246px;
        height: 280px;
    }
}

.advantages__item--functionally {
    @media (min-width: @tablet-width) {
        width: 247px;
        height: 280px;
        margin-bottom: 0;
    }
}


.advantages__item--result {
    margin-bottom: 0;
    @media (min-width: @tablet-width) {
        width: 249px;
        height: 280px;
    }
}

.advantages__images {
    position: absolute;
    @media (min-width: @tablet-width) {
        margin-top: 42px;
    }
}

.advantages__description {
    position: relative;
    margin-left: 80.5px;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #444444;
    @media (min-width: @tablet-width) {
        font-size: 16px;
        line-height: 24px;
        padding-top: 151px;
        margin: 0;
        z-index: 2;
    }
}


.advantages__description1::before {
    @media (min-width: @tablet-width) {
        content: "";
        display: block;
        position: absolute;
        background-image: url(../img/index/1.svg);
        background-repeat: no-repeat;
        width: 112px;
        height: 280px;
        left: 107px;
        top: 42px;
        z-index: -1;
    }
}

.advantages__description1::after {
        content: "";
        display: block;
        position: absolute;
        background-image: url(../img/index/block_1.svg);
        background-repeat: no-repeat;
        width: 112px;
        height: 280px;
        top: 0;
        left: -80px;
    @media (min-width: @tablet-width) {
        left: 0;
        top: 42px;
    }
}


